@import url('https://fonts.googleapis.com/css?family=Fira+Sans:300,400,500,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700');

.social-media {
  padding: 1em 0;
  text-align: center;

  @media (min-width: 60em) {
    flex: 0 0 auto;
    padding: 0;
    margin-left: auto;
    text-align: left;
  }
}

.social-media__link {
  display: inline-block;
  padding: 0.25em;

  .svg {
    //@mixin responsive-declaration width, 3em, 2.2em;
    //@mixin responsive-declaration height, 3em, 2.2em;
    width: 2.2em;
    height: 2.2em;

    path {
      fill: #FFF !important;
    }
  }

  &.ext {
    background: none;
    padding: 0.25em;
  }

  &:--enter .svg path {
    transition: fill 0.2s cubic-bezier(0.19, 1, 0.22, 1);
  }
}

.footer-top-wrapper {
  position: relative;
  z-index: 200;
}

.footer-top-wrapper__container {
  background-color: 'e8ebed;'
}

.footer-wrapper {
  position: relative;
  z-index: 100;
  color: #FFF;
}

.footer-wrapper__content {
  border-top: 0.5em solid #6cb33d;
  background-color: #2a0247;
}

.layout__content {
  padding-left: 4em;
  padding-right: 4em;
}

.footer {
  font-family: Open Sans, sans-serif;
  //@mixin responsive-declaration padding-top, 1em, 2em;
  //@mixin responsive-declaration padding-bottom, 1em, 2em;
  padding-bottom: 2em;
  padding-top: 2em;

  display: flex;
  flex-flow: column nowrap;
  align-items: center;

  @media (min-width: 60em) {
    flex-flow: row nowrap;
  }
}

.footer__logo .svg {
  width: 10em;
  height: 6em;
}

.footer__content {
  //@mixin responsive-declaration padding, 1em, 2em;
  padding: 2em;

  text-align: center;
}

.footer__paragraph {
  font-size: 0.8889em;
  padding: 1em 0;
  margin: 0;

  @media (min-width: 60em) {
    flex: 0 0 auto;
  }
}

.footer__links {
  @media print {
    display: none;
  }

  & > ul {
    margin: 0;
    padding: 0;
    text-align: center;

    @media (min-width: 50em) {
      margin: 0 -0.5em;
    }

    @media (min-width: 60em) {
      text-align: left;
      max-width: 34em;
    }

    & > li {
      display: block;
      margin: 0 0.5em;

      @media (min-width: 60em) {
        display: inline-block;
      }

      & > a {
        display: block;
        padding: 0.5em 0;
        font-size: 0.8889em;
        background-color: transparent;
        color: #FFF;
        border-radius: 0;
        transform: translateX(0%);
        box-shadow: none;
        text-decoration: underline;

        @media (min-width: 60em) {
          text-decoration: none;
        }

        :--not-touch &:--enter {
          background-color: transparent;
          /*
          box-shadow: none !important;
          */
          text-decoration: underline;
        }
      }
    }
  }
}

.page-feedback {
  .page-feeback--useful {
    font-family: fira sans,sans-serif;
    font-size: 1em;
    line-height: 1.2;
    font-weight: 700;
    color: inherit;
    margin-bottom: .5em;
    text-transform: none;
  }
  .page-feedback--yes {
    margin-left: 0.5em;
    margin-right: 0.5em;
  }
  #open-feedback {
    float: none;
    width: 100%;
    text-align: left;
    padding: 0;
    @media (min-width: 50em) {
      width: auto;
      float: right;
      line-height: 1.6;
    }
  }
  .page-feedback--p {
    padding: 1em 0;
  }
  a, button {
    color: #005e59;
    text-decoration: underline;
    cursor: pointer;
  }
}
/** Fix a weird focus issue in firefix/IE */
#webform_submission_page_feedback_node_94_add_form-ajax-content {
  display: none !important;
}
