@import 'src/lib.scss';

.two-col {
  max-width: 100%;
  &__container {
    @include grid-container;
  }

  &__row {
    @include grid-row;
    display: flex;
    flex-wrap: wrap;

    @media screen and (min-width: $fsa-md) {
      flex-direction: row;
    }
  }

  &__top {
    @include grid-cols(4, 4);
    margin-bottom: 3rem;
    @media screen and (min-width: $fsa-md) {
      @include grid-cols(12);
    }
  }
  &__left {
    @include grid-cols(4, 4);
    @media screen and (min-width: $fsa-md) {
      @include grid-cols(4);
    }
  }
  &__right {
    @include grid-cols(4, 4);
    @media screen and (min-width: $fsa-md) {
      @include grid-cols(8);
    }
  }
  &__right-top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-bottom: 2rem;
    @media screen and (min-width: $fsa-md) {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }
}
